<template>

    <div>
        <!--  -->
        <section>

        <div class="curtain"></div>

        <div class="rightCurtain1"></div>
        <div class="rightCurtain2"></div>

        </section>
        <!--  -->

        <!-- home -->
        <section>

            <div class="banner">

                <div class="bannerTitle">
                    <h2 class="h1 text-primary"></h2>
                    <h3 class="h1 text-white"></h3>
                    <h3 class="h1 text-white"></h3>
                </div>

            </div>

        </section>
        <!--  -->

        <!-- content -->
        <section>

            <div class="content my-5">
            <div class="container">

                <div class="row d-flex flex-row-reverse my-5 preFadein">
                    <div class="col-md-6 my-auto">
                        <div>
                        <h3 class="text-primary h2 mb-4">
                             
                        </h3>
                        <p class="h6 text-secondary lh-global">
                            UV  CCD  CMOS 
                        </p>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <img src="static/img/lens.jpg" alt=" " title=" Unsplash">
                    </div>
                </div>
                
                <div class="row preFadein">
                    <div class="col-md-6 my-auto">
                        <h3 class="text-primary h2 mb-4">
                             
                        </h3>
                        <p class="h6 text-secondary lh-global">
                            ,10
                        </p>
                    </div>
                    <div class="col-md-6">
                        <img src="static/img/tripod.jpg" alt=" " title=" Unsplash">
                    </div>
                </div>

            </div>
            </div>

        </section>
        <!--  -->
        
        <!-- homevideo -->
        <section>
            
            <div class="videoBanner">
            <video-bg :sources="['https://assets.mixkit.co/videos/preview/mixkit-man-walking-along-an-old-narrow-alley-tracking-27238-large.mp4']" class="preFadein">

            <div class="videoTitle">

            <vue-typer
            :text='[" ?","",""]'
            :repeat='Infinity'
            :shuffle='false'
            initial-action='typing'
            :pre-type-delay='300'
            :type-delay='200'
            :pre-erase-delay='3000'
            :erase-delay='150'
            erase-style='backspace'
            :erase-on-complete='false'
            caret-animation='blink'
            class="h2"
            ></vue-typer>

            </div>
            
            </video-bg>
            </div>

        </section>
        <!--  -->

        <!-- model -->
        <section>

            <div class="container my-5">
                <div class="row preFadein">

                    <div class="col-lg-6 d-flex justify-content-center align-items-center">
                        <div>
                        <div class="phoneAreaText">
                            <h3 class="h1 text-primary"> !!</h3>
                            <p class="h4 text-secondary my-3"> 9</p>
                            <p class="h4 text-secondary">1095</p>
                        </div>
                        </div>
                    </div>

                    <div class="col-lg-6 d-flex justify-content-center my-5">
                        <div class="phoneCase preShake">
                            <div class="phoneScreen">
                                <div class="phonePage1 preFadeinRight">
                                    <div class="text-center">
                                    <img src="static/img/email.svg" alt="email icon">
                                    <br>
                                    <h3 class="h4 text-secondary mt-3"></h3>

                                    <validation-observer v-slot="{ invalid }">
                                    <form @submit.prevent="showPhonePage2">
                                    <validation-provider rules="required|email" v-slot="{ errors , classes }">
                                    <input type="email" v-model="emailAddress" :class="classes" name="">
                                    <span class="invalid-feedback"> {{ errors[0] }} </span>
                                    </validation-provider>

                                    <button type="submit" class="btn-primary text-white mt-3 ml-auto" :disabled="invalid"></button>
                                    </form>
                                    </validation-observer>
                                    </div>
                                </div>
                                <div class="phonePage2">
                                    <div class="text-center">
                                    <img src="static/img/thank-you.svg" alt="thank you">
                                    </div>
                                    <h3 class="h2 text-secondary"></h3>
                                    <p class="h5 text-secondary my-4">9</p>
                                    <p class="h2 text-primary couponCode">10off_sale</p>
                                </div>
                            </div>
                            <div class="topCamera"></div>
                            <div class="phoneBottomLine"></div>
                        </div>
                    </div>

                </div>
            </div>

        </section>
        <!--  -->

    </div>

</template>

<script>

import $ from 'jquery';

export default {
    data () {
        return {
            emailAddress : ''
        }
    },
    methods : {
        openPage(){
            setTimeout(() => {
                $('.curtain').addClass('curtainShow');
            }, 5);
        },
        addAnimate(){
            const scrollPos = $(window).scrollTop();
            const windowHeight = $(window).height();

            $('.preFadein').each(function(){
                const itemPos = $(this).offset().top;

                if((scrollPos + windowHeight) > (itemPos + 100)){
                    $(this).addClass('fadeIn');
                }
            })

            $('.preFadeinRight').each(function(){
                const itemPos = $(this).offset().top;

                if((scrollPos + windowHeight) > (itemPos + 100)){
                    $(this).addClass('fadeIn');
                }
            })

            $('.preFadeinLeft').each(function(){
                const itemPos = $(this).offset().top;

                if((scrollPos + windowHeight) > (itemPos + 100)){
                    $(this).addClass('fadeIn');
                }
            })

            $('.preShake').each(function(){
                const itemPos = $(this).offset().top;

                if((scrollPos + windowHeight) > (itemPos + 150)){
                    $(this).addClass('shake');
                }
            })
        },
        showPhonePage2(){
            $('.phonePage2').addClass('fadeIn')
        }
    },
    created(){
        this.openPage();
        window.addEventListener('scroll' , this.addAnimate);
    }
}
</script>


